* {
  cursor: none;
  margin: 0;
  padding: 0;
}
body,
html {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
}
.container {
  max-width: 1240px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  padding: 0 50px;
}
.header {
  position: fixed;
  top: -100px;
  left: 0;
  right: 0;
  z-index: 10;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  animation: 1s forwards;
  animation-name: slidein;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px !important;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.header__logo {
  display: block;
  width: 50px;
  height: 50px;
  font-size: 0;
  text-decoration: none;
  background-image: url("../img/z.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.header__item {
  display: block;
  font-size: 21px;
  line-height: 25px;
  padding-right: 35px;
  text-decoration: none;
  color: black;
  margin-left: 15px;
  transition: opacity .2s;
  background-position: right top;
}
.header__item:last-child {
  padding-right: 0;
}
.header__item:hover {
  opacity: 0.8;
}
.header__item::before,
.header__item::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}
.header__item::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}
.header__item::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}
.header__item:hover::before,
.header__item:hover::after,
.header__item:focus::before,
.header__item:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}
.header__toggle {
  display: none;
  width: 65px;
  height: 25px;
  font-size: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: opacity .2s;
  background-image: url("../img/icon__burger.svg");
}
.header__toggle:hover {
  opacity: 0.8;
}
.iam {
  display: flex;
  align-items: center;
  padding-top: 120px;
}
.iam__image {
  display: block;
  width: 450px;
  height: 450px;
  background-image: url("../img/me.jpeg");
  -webkit-filter: grayscale(1%);
  filter: grayscale(1%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin-right: -50px;
}
.iam__image .cursor_big {
  position: relative;
  z-index: 10;
}
.iam__descr {
  position: relative;
  top: -100px;
  pointer-events: none;
  float: right;
  font-weight: 600;
  font-size: 60px;
  line-height: 80px;
  width: 800px;
  z-index: 5;
  animation: 2s slidein forwards;
}
.iam p {
  margin: 0;
  padding: 0;
}
.experience {
  font-size: 15px;
  font-weight: normal;
  line-height: 30px;
}
.experience__container {
  display: flex;
  justify-content: space-between;
  text-align: left;
  margin-top: 120px;
}
.experience__item {
  max-width: 350px;
}
.experience__title {
  margin-top: 10px;
  color: red;
}
.experience__year {
  margin-top: 15px;
  color: grey;
}
.myskills__container {
  max-width: 100%;
  margin-top: 120px;
  padding: 0;
  overflow: hidden;
}
.myskills .slideshow {
  width: 100%;
}
.myskills .slide {
  font-size: 80px;
  line-height: 105px;
  font-weight: 800;
  margin: 0 32px;
}
@supports (-webkit-text-stroke: 1px black) {
  .myskills .slide {
    -webkit-text-stroke: 1px black;
    -webkit-text-fill-color: white;
  }
}
.myprojects {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 120px;
}
.myprojects__project {
  max-width: calc(48%);
  margin-bottom: 50px;
}
.myprojects__descr {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 300px;
}
.myprojects__descr .overlay {
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
}
.myprojects__descr:hover .overlay {
  opacity: 1;
  transition: .5s ease;
}
.myprojects__descr span {
  font-size: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-weight: 500;
  padding: 50px;
  box-sizing: border-box;
  height: 100%;
  text-align: center;
}
@keyframes slidein {
  0% {
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}
.cursor-dot,
.cursor-dot-outline {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.cursor-dot {
  width: 8px;
  height: 8px;
  background-color: #ff4040;
}
.cursor-dot-outline {
  width: 40px;
  height: 40px;
  border: solid 1px #FF0000;
}
